.dropdown-facilities {
  position: relative;
  width: 100%;
}
.dropdown-facilities__label {
  text-transform: uppercase;
  font-size: 0.75rem;
  line-height: 1.5rem;
  font-weight: bold;
  color: #1f2041;
}
.dropdown-facilities__menu {
  position: absolute;
  z-index: 100;
  top: 2.65rem;
  left: 0;
  width: 100%;
  padding-top: 0.25rem;
  border: 1px solid #8f90A0;
  border-top-color: #c7c7cF;
  border-radius: 0 0 4px 4px;
  background-color: #fff;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
}
.dropdown-facilities__menu-list {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 7.1rem;
}
.dropdown-facilities__menu-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 2.4rem;
  padding: 0 0.35rem 0.3rem 0.9rem;
  text-transform: uppercase;
  font-size: 0.75rem;
  font-weight: bold;
}
.dropdown__menu_disabled {
  display: none;
}
