.dropdown-counter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 5.75rem;
}
.dropdown-counter__value {
  font-size: 0.75rem;
  margin-bottom: 0.2rem;
}
.dropdown-counter__button-minus {
  height: 1.875rem;
  width: 1.875rem;
  border: 1px solid #8f90A0;
  border-radius: 50%;
  cursor: pointer;
}
.dropdown-counter__button-plus {
  height: 1.875rem;
  width: 1.875rem;
  border: 1px solid #8f90A0;
  border-radius: 50%;
  cursor: pointer;
}
.dropdown-counter__button_disabled {
  border: 1px solid #c7c7cF;
}
.dropdown-counter__minus {
  color: #8f90A0;
  font-size: 1.125rem;
  line-height: 1.8rem;
}
.dropdown-counter__plus {
  color: #8f90A0;
  font-size: 1.125rem;
  line-height: 1.95rem;
}
