.button_filled {
  height: 2.75rem;
  min-width: 6.188rem;
  border-radius: 22px;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  cursor: pointer;
}
.button_filled:hover {
  opacity: 0.5;
}
.button_filled:active {
  opacity: 1;
}
.button_filled__message {
  margin-right: 0.05rem;
  font-size: 0.7rem;
  font-weight: bold;
  line-height: 2.2rem;
  letter-spacing: 0.1px;
  text-transform: uppercase;
  color: #fff;
}
